*{
  margin: 0%;
  padding: 0%;
}
.btn-info{
  background-color: rgb(5, 82, 82);
  padding: 5px 15px;
  color:#fff;
}

.left-menu{
  display: none;
  float: right;
  margin-right: 10px !important;
  cursor:pointer;
}

.btn-info:hover{
  color: rgb(5, 82, 82);
  background-color:rgb(98, 213, 213);
  border:0.5px solid rgb(5, 82, 82);
}

p{
  text-align: justify;
  padding: 10px 5px;
}

h1{
  padding: 10px;
}

strong{
  font-size: 32px;
}

/*Header Section Styling  */

.nav{
  /* background-color: rgb(98, 213, 213); */
  background: linear-gradient(90deg, rgba(40,145,204,1) 0%, rgba(96,221,176,1) 100%, rgba(0,212,255,1) 100%);
  padding:15px;   
}

.nav img{
  height:60px;
  width:60px;
  margin-right: 170px;
}

.mobile-menu{
  display:none;
}

ul{
  width:100%;
}

ul li{
  text-align: right;
  list-style-type:none;
  display: inline;
  margin:20px;
}

.link:hover{
  color: rgb(5, 82, 82);
  background-color:rgb(98, 213, 213);
  border:0.5px solid rgb(5, 82, 82);
}
.link{
  text-decoration: none;
  color:#fff;
  padding: 5px 10px;
  background:rgb(5, 82, 82);
  border:0.5px solid rgb(5, 82, 82);
}


.Home-img{
  height:450px;
  width:500px;
}

/* Services Section */
.service-box{
  padding: 50px;
  box-shadow: -10px 0px 8px -10px #055252, 10px 0px 8px -10px #055252, 0px 0px 3px 2px #055252;
  height:300px;
  border-radius: 10px;
}
.contact-section{
  margin: 50px;
}

.home-form{
  border:1px solid red;
}

.service-left-img{
  height:500px !important;
  width:500px !important;
}

.home-form-box{
  margin: 25px;
  box-shadow: -10px 0px 13px -7px #055252, 10px 0px 13px -7px #055252, 0px 0px 3px 2px #fff;
  border-radius: 10px;
  padding: 40px;
}

.service-text{
  font-size:20px;
  color:#055252;
}

/* Footer Section Styling starts here */

.Footer-section{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width:100%;
  background-color: #62d5d5;
}

.footer-middle-section{
  justify-content: space-around;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 70%;
}

.icons{
  height:50px;
  width:50px;
}

.whatsapp{
  float:right;
}


.modal{
  display:none;
  width: 400px;
  height: 450px;
  position: fixed;
  top:20%;
  left:35%;
  background-color: #fff;
  box-shadow: -10px 0px 13px -7px #055252, 10px 0px 13px -7px #055252, 0px 0px 3px 2px #fff;
  border-radius: 5px;
 
}

.close{
  width: fit-content;
  float: right;
  padding-top: 3px;
  margin: -30px 0px 0px 350px;
  border-radius: 20px;
  background: #055252;
  color: #fff;
  cursor: pointer;
}

.thans-img{
  width:450px;
  height:450px;
}

/* Responsive Css Starts  */

@media(max-width:576px){

  .modal{
      left:13%;
      width:300px;
  }
  #abt1{
    order:1;
  }
  #abt2{
    order:2;
  }
  #abt3{
    order:3;
  }
  #d1{
      order:2;
  }

  #t1{
      order:2;
  }

  .list {
      display:none;
  }
  ul li{
      text-align: left;
      margin: 15px 0px 0px 0px;
  }
  .home-form-box{
      margin: -30px -30px 28px -30px;
  }
 .left-menu{
  display: block;
 }

  ul{
      background-color: rgb(5, 82, 82);
      padding: 10px;
      text-align: center;
  }
  .Home-img{
      width:300px !important;
      height:300px !important;
  }
  
  .close{
      margin: -30px 0px 0px 250px;
  }

  .thans-img{
      width:300px;
      height:300px;
  }
}

@media(min-width:576px){
  .mobile-menu{
          display:none;
  }
}